.redaction-section {
    margin-bottom: clamp(80px, 10.417vw, 150px);

    @include tablet {
        .container {
            padding: 0;
        }
    }

    &__post {
        display: flex;
        align-items: stretch;

        @include tablet {
            flex-direction: column;
        }

        &-image {
            display: flex;
            width: 100%;

            picture,
            img {
                width: 100%;
            }

            @include for-tablet {
                max-width: 602px;
            }    
        }

        &-info {
            flex-grow: 1;
            padding: 20px;
            background-color: var(--cl-c);
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: clamp(24px, 8.301vw, 85px);
        }

        &-top {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            flex-wrap: wrap;
            gap: 17px 10px;
        }

        &-heading {
            color: var(--cl-a);
        }

        &-details {
            display: flex;
            align-items: center;
            gap: 4px;
        }

        &-views,
        &-author,
        &-pubtime {
            width: fit-content;
            display: flex;
            align-items: center;
            font-size: 12px;
            line-height: normal;
            font-weight: 300;
            color: var(--cl-a);

            svg {
                stroke: var(--cl-f);
            }
        }

        &-box {
            max-width: 555px;
            display: flex;
            flex-direction: column;
            gap: clamp(8px, 1.563vw, 16px);
        }

        &-controls {
            display: flex;
            align-items: center;
            justify-content: space-between;

            @include tablet {
                align-items: flex-start;
            }
        }

        &-link {
            display: none;

            @include tablet {
                display: flex;
            }
        }

        &-tag {
            display: flex;
            align-items: center;
            font-size: 12px;
            line-height: normal;
            text-transform: lowercase;
            color: rgba(255, 255, 255, 0.70);

            svg {
                stroke: rgba(255, 255, 255, 0.70);
            }
        }

        &-title {
            font-size: clamp(16px, 1.758vw, 18px);
            line-height: 160%;
            font-weight: 500;
            color: var(--cl-a);
        }

        &-excerpt {
            font-size: clamp(14px, 1.563vw, 16px);
            line-height: 160%;
            font-weight: 500;
            color: rgba(255, 255, 255, 0.70);
        }
    }
}

html[data-theme="dark"] {
    .redaction-section {
        &__post {
            &-info {
                background-color: var(--cl-n);
            }

            &-views,
            &-author,
            &-pubtime {
                color: var(--cl-b);
    
                svg {
                    stroke: var(--cl-b);
                }
            }

            &-tag {
                color: rgba(255, 255, 255, 0.70);

                svg {
                    stroke: rgba(255, 255, 255, 0.70);
                }
            }

            &-title {
                color: var(--cl-b);
            }

            &-excerpt {
                color: rgba(255, 255, 255, 0.70);
            }
        }
    }
}