    flex-direction: column;
  }
}
.search-section__proposals {
  padding: 12px 16px;
  display: flex;
  align-items: center;
  gap: 12px;
  background-color: var(--cl-k);
  border-radius: 4px;
}
.search-section__proposals svg {
  flex-shrink: 0;
  stroke: var(--cl-f);
}
.search-section__proposals p {
  font-size: 16px;
  line-height: 125%;
  font-weight: 300;
}
.search-section__proposals a {
  font-size: 16px;
  line-height: 125%;
  font-weight: 700;
  color: var(--cl-s);
  text-decoration-line: underline;
  text-decoration-style: solid;
  text-decoration-skip-ink: none;
  text-decoration-thickness: auto;
  text-underline-offset: auto;
}
.search-section__link {
  height: 43px;
  max-width: 343px;
  width: 100%;
  background-color: var(--cl-c);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  line-height: normal;
  font-weight: 500;
  color: var(--cl-a);
  text-align: center;
}
@media (max-width: 1024px) {
  .search-section__link {
    max-width: 100%;
  }
}

html[data-theme=dark] .search-section__image svg {
  stroke: var(--cl-d);
}
html[data-theme=dark] .search-section__info li {
  color: var(--cl-b);
}
html[data-theme=dark] .search-section__proposals {
  background-color: var(--cl-h);
}
html[data-theme=dark] .search-section__proposals svg {
  stroke: var(--cl-d);
}
html[data-theme=dark] .search-section__proposals p {
  color: var(--cl-b);
}
html[data-theme=dark] .search-section__proposals a {
  color: var(--cl-b);
}
html[data-theme=dark] .search-section__link {
  background-color: var(--cl-n);
  color: var(--cl-b);
}

.info-section {
  padding-top: 32px;
  margin-bottom: clamp(80px, 10.417vw, 150px);
}
.info-section__wrapp {
  position: relative;
  display: grid;
  gap: clamp(24px, 2.778vw, 40px);
}
@media (min-width: 1025px) {
  .info-section__wrapp {
    grid-template-columns: clamp(300px, 26.528vw, 382px) 1fr;
    align-items: flex-start;
  }
}
@media (max-width: 1024px) {
  .info-section__wrapp {
    grid-template-columns: 100%;
  }
}
.info-section__aside {
  padding: clamp(16px, 2.344vw, 24px) 16px;
  background-color: var(--cl-k);
  border-radius: 16px;
}
@media (min-width: 1025px) {
  .info-section__aside {
    position: sticky;
    top: var(--header-top);
  }
}
.info-section__pages {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.info-section__pages a {
  padding: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
  font-size: 16px;
  line-height: normal;
  font-weight: 500;
  color: var(--cl-c);
}
.info-section__pages a svg {
  stroke: var(--cl-c);
}
.info-section__box > h3:first-of-type {
  margin-top: 0;
}
.info-section__box h3 {
  margin: 40px 0 24px;
  font-size: clamp(18px, 2.344vw, 24px);
  line-height: 140%;
  font-weight: 700;
  color: var(--cl-c);
}
.info-section__box p {
  font-size: 16px;
  line-height: 160%;
  font-weight: 300;
  color: var(--cl-c);
}
.info-section__box ul li {
  list-style-type: disc;
  list-style-position: inside;
  font-size: 16px;
  line-height: 160%;
  font-weight: 300;
  color: var(--cl-c);
}

html[data-theme=dark] .info-section__aside {
  background-color: var(--cl-h);
}
html[data-theme=dark] .info-section__pages a {
  color: var(--cl-b);
}
html[data-theme=dark] .info-section__pages a svg {
  stroke: var(--cl-b);
}
html[data-theme=dark] .info-section__box h3 {
  color: var(--cl-b);
}
html[data-theme=dark] .info-section__box p {
  color: var(--cl-b);
}
html[data-theme=dark] .info-section__box ul li {
  color: var(--cl-b);
}